"use client"

import type React from "react"

import { useState } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import { ArrowLeft, Loader2, CreditCard, DollarSign, X } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { useCart } from "@/contexts/cart-context"
import { useAuth } from "@/contexts/auth-context"
import { useCreateCommandeClient } from "@/hooks/use-api"

const CAMEROON_CITIES = [
  "Douala",
  "Yaound",
  "Garoua",
  "Bamenda",
  "Limbe",
  "Kumba",
  "Bafoussam",
  "Buea",
  "Foumban",
  "Ebolowa",
]

export default function CheckoutPage() {
  const router = useRouter()
  const { user, isAuthenticated } = useAuth()
  const { items, totalAmount, clearCart } = useCart()
  const { mutate: createOrder, isPending } = useCreateCommandeClient()

  // Form state
  const [formData, setFormData] = useState({
    firstName: user?.name?.split(" ")[0] ?? "",
    lastName: user?.name?.split(" ").slice(1).join(" ") ?? "",
    email: user?.email ?? "",
    phone: user?.phoneNumber ?? "",
    city: user?.town ?? "Douala",
    address: user?.address ?? "",
    postalCode: "",
    paymentMethod: "cash",
  })

  const [errors, setErrors] = useState<Record<string, string>>({})
  const [isOpen, setIsOpen] = useState(true)

  // Redirect if not authenticated or cart is empty
  if (!isAuthenticated || !user || items.length === 0) {
    router.push("/")
    return null
  }

  const validateForm = () => {
    const newErrors: Record<string, string> = {}

    if (!formData.firstName.trim()) newErrors.firstName = "Le prnom est requis"
    if (!formData.lastName.trim()) newErrors.lastName = "Le nom est requis"
    if (!formData.email.trim()) newErrors.email = "L'email est requis"
    if (!formData.phone.trim()) newErrors.phone = "Le tlphone est requis"
    if (!formData.city) newErrors.city = "La ville est requise"
    if (!formData.address.trim()) newErrors.address = "L'adresse est requise"

    setErrors(newErrors)
    return Object.keys(newErrors).length === 0
  }

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target
    setFormData((prev) => ({ ...prev, [name]: value }))
    if (errors[name]) {
      setErrors((prev) => {
        const { [name]: _, ...rest } = prev
        return rest
      })
    }
  }

  const handleClose = () => {
    setIsOpen(false)
    router.back()
  }

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()

    if (!validateForm()) return

    // Create order object
    const orderData = {
      codeCommande: `CMD-${Date.now()}`,
      dateCommande: new Date(),
      clientNom: `${formData.firstName} ${formData.lastName}`,
      clientEmail: formData.email,
      clientTelephone: formData.phone,
      adresseLivraison: formData.address,
      villeLivraison: formData.city,
      codePostal: formData.postalCode,
      methodePaiement: formData.paymentMethod,
      montantTotal: totalAmount,
      statusCommande: "PENDING",
      lignesCommande: items.map((item) => ({
        articleId: item.articleId,
        quantite: item.quantite,
        prixUnitaire: item.prixUnitaire,
        sousTotal: item.sousTotal,
      })),
    }

    createOrder(orderData as any, {
      onSuccess: () => {
        clearCart()
        router.push(`/commande-confirmation/${orderData.codeCommande}`)
      },
      onError: (error) => {
        console.error("Order creation error:", error)
        setErrors({ submit: "Erreur lors de la cration de la commande. Veuillez ressayer." })
      },
    })
  }

  const shippingCost = totalAmount > 50000 ? 0 : 2500
  const finalTotal = totalAmount + shippingCost

  return (
    <Dialog open={isOpen} onOpenChange={setIsOpen}>
      <DialogContent className="max-w-4xl max-h-[90vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle className="flex items-center justify-between">
            Confirmation de la commande
            <Button variant="ghost" size="icon" onClick={handleClose}>
              <X className="h-4 w-4" />
            </Button>
          </DialogTitle>
        </DialogHeader>

        <div className="grid lg:grid-cols-3 gap-6">
          {/* Form Section */}
          <div className="lg:col-span-2">
            <form onSubmit={handleSubmit} className="space-y-6">
              {/* Error Alert */}
              {errors.submit && (
                <Alert variant="destructive">
                  <AlertDescription>{errors.submit}</AlertDescription>
                </Alert>
              )}

              {/* Name Fields */}
              <div className="grid sm:grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="firstName">Prnom</Label>
                  <Input
                    id="firstName"
                    name="firstName"
                    value={formData.firstName}
                    onChange={handleInputChange}
                    className={errors.firstName ? "border-destructive" : ""}
                    placeholder="Votre prnom"
                  />
                  {errors.firstName && <p className="text-sm text-destructive">{errors.firstName}</p>}
                </div>
                <div className="space-y-2">
                  <Label htmlFor="lastName">Nom</Label>
                  <Input
                    id="lastName"
                    name="lastName"
                    value={formData.lastName}
                    onChange={handleInputChange}
                    className={errors.lastName ? "border-destructive" : ""}
                    placeholder="Votre nom"
                  />
                  {errors.lastName && <p className="text-sm text-destructive">{errors.lastName}</p>}
                </div>
              </div>

              {/* Contact Fields */}
              <div className="grid sm:grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="email">Email</Label>
                  <Input
                    id="email"
                    name="email"
                    type="email"
                    value={formData.email}
                    onChange={handleInputChange}
                    className={errors.email ? "border-destructive" : ""}
                    placeholder="exemple@email.com"
                  />
                  {errors.email && <p className="text-sm text-destructive">{errors.email}</p>}
                </div>
                <div className="space-y-2">
                  <Label htmlFor="phone">Tlphone</Label>
                  <Input
                    id="phone"
                    name="phone"
                    value={formData.phone}
                    onChange={handleInputChange}
                    className={errors.phone ? "border-destructive" : ""}
                    placeholder="+237 6 XX XX XX XX"
                  />
                  {errors.phone && <p className="text-sm text-destructive">{errors.phone}</p>}
                </div>
              </div>

              {/* Address Fields */}
              <div className="space-y-2">
                <Label htmlFor="address">Adresse</Label>
                <Input
                  id="address"
                  name="address"
                  value={formData.address}
                  onChange={handleInputChange}
                  className={errors.address ? "border-destructive" : ""}
                  placeholder="Rue, btiment, etc."
                />
                {errors.address && <p className="text-sm text-destructive">{errors.address}</p>}
              </div>

              {/* City and Postal Code */}
              <div className="grid sm:grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="city">Ville</Label>
                  <Select
                    value={formData.city}
                    onValueChange={(value) => setFormData((prev) => ({ ...prev, city: value }))}
                  >
                    <SelectTrigger>
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      {CAMEROON_CITIES.map((city) => (
                        <SelectItem key={city} value={city}>
                          {city}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                  {errors.city && <p className="text-sm text-destructive">{errors.city}</p>}
                </div>
                <div className="space-y-2">
                  <Label htmlFor="postalCode">Code postal</Label>
                  <Input
                    id="postalCode"
                    name="postalCode"
                    value={formData.postalCode}
                    onChange={handleInputChange}
                    placeholder="Code postal (optionnel)"
                  />
                </div>
              </div>

              {/* Payment Method */}
              <div className="space-y-4">
                <h3 className="font-semibold flex items-center gap-2">
                  <CreditCard className="h-5 w-5" />
                  Mthode de paiement
                </h3>
                <RadioGroup
                  value={formData.paymentMethod}
                  onValueChange={(value) => setFormData((prev) => ({ ...prev, paymentMethod: value }))}
                >
                  <div className="flex items-center space-x-2 p-3 rounded-lg border border-border hover:border-primary cursor-pointer">
                    <RadioGroupItem value="cash" id="cash" />
                    <Label htmlFor="cash" className="flex-1 cursor-pointer">
                      <div className="font-medium">Paiement  la livraison</div>
                      <p className="text-sm text-muted-foreground">Payez en espces lors de la livraison</p>
                    </Label>
                  </div>
                  <div className="flex items-center space-x-2 p-3 rounded-lg border border-border hover:border-primary cursor-pointer opacity-50">
                    <RadioGroupItem value="card" id="card" disabled />
                    <Label htmlFor="card" className="flex-1 cursor-pointer opacity-50">
                      <div className="font-medium">Carte bancaire</div>
                      <p className="text-sm text-muted-foreground">Indisponible pour le moment</p>
                    </Label>
                  </div>
                </RadioGroup>
              </div>

              {/* Submit Button */}
              <Button type="submit" className="w-full gradient-primary text-white" size="lg" disabled={isPending}>
                {isPending ? (
                  <>
                    <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                    Cration de la commande...
                  </>
                ) : (
                  <>
                    <DollarSign className="mr-2 h-4 w-4" />
                    Confirmer la commande ({finalTotal.toLocaleString()} FCFA)
                  </>
                )}
              </Button>
            </form>
          </div>

          {/* Order Summary Sidebar */}
          <div className="lg:col-span-1">
            <Card className="bg-card/50 backdrop-blur">
              <CardHeader>
                <CardTitle>Rsum de la commande</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                {/* Items */}
                <div className="space-y-3 max-h-64 overflow-y-auto">
                  {items.map((item) => (
                    <div key={item.articleId} className="flex justify-between text-sm">
                      <div>
                        <p className="font-medium line-clamp-1">{item.articleNom}</p>
                        <p className="text-muted-foreground text-xs">x{item.quantite}</p>
                      </div>
                      <p className="font-semibold text-right">{item.sousTotal?.toLocaleString()} FCFA</p>
                    </div>
                  ))}
                </div>

                <div className="border-t border-border" />

                {/* Pricing */}
                <div className="space-y-2 text-sm">
                  <div className="flex justify-between">
                    <span className="text-muted-foreground">Sous-total</span>
                    <span>{totalAmount.toLocaleString()} FCFA</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-muted-foreground">Livraison</span>
                    <span className={shippingCost === 0 ? "text-green-500 font-medium" : ""}>
                      {shippingCost === 0 ? "Gratuite" : `${shippingCost.toLocaleString()} FCFA`}
                    </span>
                  </div>
                </div>

                {shippingCost > 0 && (
                  <p className="text-xs text-muted-foreground bg-secondary/30 p-2 rounded">
                    Livraison gratuite  partir de 50 000 FCFA
                  </p>
                )}

                <div className="border-t border-border pt-2">
                  <div className="flex justify-between text-lg font-bold">
                    <span>Total</span>
                    <span className="text-primary">{finalTotal.toLocaleString()} FCFA</span>
                  </div>
                </div>
              </CardContent>
            </Card>
          </div>
        </div>
      </DialogContent>
    </Dialog>
  )
}